 max-width: 1036px;\n    width: 100%;\n    background: $color__n-85;\n    border: 1px solid $color__n-60;\n    border-radius: $border-radius__base;\n    color: $color__text;\n\n    .tile--body {\n        padding-top: $spacing__s;\n        display: flex;\n        flex-direction: column-reverse;\n        gap: 0;\n\n        header {\n            padding: $spacing__m;\n            display: flex;\n            flex-direction: column;\n            gap: $spacing__m;\n        }\n\n        a.tile--body--logo {\n            max-width: 90px;\n            max-height: 90px;\n            border-radius: 50%;\n            margin: auto;\n\n            img {\n                object-fit: cover;\n                border: 1px solid $color__n-60;\n                border-radius: 50%;\n                background: white;\n                min-width: 88px;\n                min-height: 88px;\n                max-width: 88px;\n                max-height: 88px;\n            }\n        }\n        img.tile--body--logo {\n            object-fit: cover;\n            border-radius: 50%;\n            background: white;\n            border: 1px solid $color__n-60;\n            min-width: 88px;\n            min-height: 88px;\n            max-width: 88px;\n            max-height: 88px;\n            margin: auto;\n        }\n        &--botracestatus {\n            display: inline-flex;\n            gap: 0.5rem;\n            align-items: center;\n            color: $color__white;\n            font-weight: 700;\n        }\n        &--period, &--description {\n            display: block;\n            text-align: center;\n        }\n        &--title {\n            margin: 0rem;\n            color: $color__white;\n            text-align: center;\n        }\n        &--description {\n            margin: $spacing__s 0rem 0rem;\n        }\n        &--amount {\n            margin: 0rem;\n            border-bottom: 1px solid $color__n-60;\n            padding: 0rem $spacing__m $spacing__s;\n            text-align: right;\n            color: $color__white;\n            font-weight: 500px;\n            font-size: $font-size__copy;\n        }\n\n        @include breakpoint(\"xxs\") {\n            header {\n                flex-direction: row;\n            }\n\n            a.tile--body--logo, img.tile--body--logo {\n                margin: unset;\n            }\n\n            &--period, &--description, &--title {\n                text-align: left;\n            }\n        }\n\n        @include breakpoint(\"m\") {\n            padding: $spacing__m;\n            flex-direction: row;\n            gap: $spacing__xl;\n\n            header {\n                padding: 0rem;\n            }\n            &--amount {\n                border-bottom: none;\n                padding: 0rem;\n            }\n        }\n    }\n\n    .tile--footer {\n        border-top: 1px solid $color__n-60;\n        display: flex;\n        flex-wrap: wrap;\n        flex-direction: row;\n        height: fit-content;\n\n        &--details {\n            display: flex;\n            flex-direction: row;\n            width: 100%;\n        }\n\n        &--status, &--timer, &--contestredirect {\n            padding: $spacing__s $spacing__m;\n        }\n        &--status {\n            justify-content: flex-start;\n            width: max-content;\n            border-right: 1px solid $color__n-60;\n\n            &.ended {\n                border-right: 0px;\n            }\n        }\n        &--timer {\n            width:  100%;\n            margin: 0px;\n        }\n        &--options {\n            display: flex;\n            flex-direction: row;\n            width: 100%;\n        }\n\n        &--contestredirect {\n            text-align: center;\n            width: 100%;\n            border-top: 1px solid $color__n-60;\n            text-decoration: none;\n            color: $color__white;\n\n            &:hover {\n                background: $color__n-80;\n            }\n        }\n        &--dropdown {\n            border